import { Injectable } from '@angular/core';
import { registerCandidates } from '../../models/registerCandidates'
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class RegisterLoginService {

  readonly baseUrl = 'http://localhost:5000/candidates/register';
  private loginUrl = 'http://localhost:5000/candidates/login';

  constructor(private http : HttpClient) { }

  postCandidates(regCand : registerCandidates){
    return this.http.post(this.baseUrl, regCand);
  }

  loginCandidate(loginCredentials : Object){
    return this.http.post(this.loginUrl, loginCredentials);
  }


}
