import { Component, OnInit } from '@angular/core';
import { Validators, FormGroup, FormBuilder, FormControl } from '@angular/forms';
// import { CustomvalidationService } from '../services/customvalidation.service';
import { Router } from '@angular/router';
import { RegisterLoginService } from 'src/app/services/registerLogin/register-login.service';
import { registerCandidates } from '../../models/registerCandidates';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerForm:FormGroup = new FormGroup({});

  constructor(public router:Router,private fb: FormBuilder, public regLoginService:RegisterLoginService) { }

  ngOnInit(): void {

    this.registerForm = this.fb.group({
      firstname: ['', Validators.required],
      lastname: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      mobile: ['', [Validators.required, Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$")]],
      // mobile:['', [Validators.required, Validators.minLength(10), Validators.maxLength(10)]],
      // username: ['', [Validators.required], this.customValidator.userNameValidator.bind(this.customValidator)],
      password: ['', Validators.compose([Validators.required])],
      confirmPassword: ['', Validators.compose([Validators.required])]
      
    })
  }


  get f(){
    return this.registerForm.controls;
  }

  readValuesFromForm(f:HTMLFormElement){

    // let _id = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('_id')).value; 
    let firstname = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('firstname')).value;
    let lastname = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('lastname')).value;
    let email = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('email')).value;
    let mobile = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('mobile')).value;
    let password = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('password')).value;
    let confirmPassword = (<HTMLInputElement>f.getElementsByTagName("input").namedItem('confirmPassword')).value;

    let regCandidates : registerCandidates = {
      firstname:firstname,
      lastname:lastname,
      email:email,
      mobile:mobile,
      password:password,
      confirmPassword:confirmPassword


    }

    return regCandidates;

  }

  empSubmit(event : Event){

    event.preventDefault();

    let form = <HTMLFormElement>event.target;
    let registeringEmployee = this.readValuesFromForm(form);

    console.log(registeringEmployee);

    this.regLoginService.postCandidates(registeringEmployee).subscribe((res)=>{
      console.log(res);
      
      this.router.navigateByUrl('/candidates/login');
    })
    

  }

}
