import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { QuestionsService } from 'src/app/services/questions/questions.service';

@Component({
  selector: 'app-finish-test',
  templateUrl: './finish-test.component.html',
  styleUrls: ['./finish-test.component.css']
})
export class FinishTestComponent implements OnInit {

  correctAnswers:any;

  constructor(public questionService:QuestionsService, private router:Router) { 
    this.questionService.finishTest().subscribe((res)=>{
      console.log("Total correct answers given : "+res);

      this.correctAnswers = res;
      
    })
  }

  ngOnInit(): void {
  }

  leaveEventHandler(){
    this.router.navigateByUrl('candidates/login');
  }

  
}
