import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FinishTestComponent } from './components/finish-test/finish-test.component';


import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { QuestionsComponent } from './components/questions/questions.component';
import { RegisterComponent } from './components/register/register.component';
import { StartTestComponent } from './components/start-test/start-test.component';


const routes: Routes = [

  {path:"candidates/register", component:RegisterComponent},
  {path:"candidates/login", component:LoginComponent},
  {path:"home", component:HomeComponent},
  {path:"candidates/startTest", component:StartTestComponent},
  {path:"questions/:questionNumber", component:QuestionsComponent},
  {path:"finish", component:FinishTestComponent},
  {path:"", redirectTo:"/home", pathMatch:"full"}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
