import { Component, OnInit } from '@angular/core';

import { Validators, FormGroup, FormBuilder, FormControl } from '@angular/forms';
// import { CustomvalidationService } from '../services/customvalidation.service';
import { Router } from '@angular/router';
import { RegisterLoginService } from 'src/app/services/registerLogin/register-login.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginForm:FormGroup = new FormGroup({});

  constructor(public router:Router,private fb: FormBuilder, public regLoginService:RegisterLoginService) { }

  ngOnInit(): void {

    this.loginForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      
      password: ['', Validators.compose([Validators.required])]
      
    })
  }

  readLoginCredentials(form:HTMLFormElement){

    let email = (<HTMLInputElement>form.getElementsByTagName("input").namedItem('email')).value;

    let password = (<HTMLInputElement>form.getElementsByTagName("input").namedItem('password')).value;

    let loginCredentials = {
      email:email,
      password:password
    }

    return loginCredentials;

  }

  candidateLogin(event : Event){

    event.preventDefault();
    let form  = <HTMLFormElement>event.target;

    let loginCandidate = this.readLoginCredentials(form);

    console.log(loginCandidate);

    this.regLoginService.loginCandidate(loginCandidate).subscribe((res)=>{
      console.log(res);

      this.router.navigateByUrl('/candidates/startTest');
      
    })
    
  }

}
